import logo from './crest.svg';
import './App.css';
import { useRef, useState } from 'react';

function App() {
  const [code, setCode] = useState(null);
  const codes = {
    'MONKEY': '010 202',
    'BANANA': '291 020',
  };

  console.log('Code: ' + code);

  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />

        {code === null ? (
          <>
            <p>
              Enter decoded password below to get your next coordinates.
            </p>

            <CodeInput codes={codes} setCode={setCode} />
          </>
        ) : (
          <>
            <p>
              Encode the below coordinates and send them to your team.
            </p>

            <CoordinateOutput coordinates={codes[code]} setCode={setCode} />
          </>
        )}
      </header>
    </div>
  );
}

function CodeInput({ codes, setCode }) {
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState('');
  const codeRef = useRef();

  const invalidCode = () => {
    setState('invalid');
    setTimeout(() => {
      codeRef.current.value = '';
      setState('');
      setDisabled(false);
    }, 2000);
  };

  const validCode = (code) => {
    setState('valid');
    setTimeout(() => {
      setCode(code);
    }, 2000);
  };

  const onKeyUp = (event) => {
    const code = codeRef.current.value.toUpperCase();
    console.log(codes, code, codes[code])
    if (event.key === 'Enter') {
      setDisabled(true);

      if (!codes[code]) {
        invalidCode();
      } else {
        validCode(code);
      }
    } else {
      setState('');
    }
  };

  return (
    <input
      className={`code-input ${state}`}
      type="text"
      placeholder="Enter code..."
      ref={codeRef}
      onKeyUp={onKeyUp}
      disabled={disabled}
    />
  )
}

function CoordinateOutput({ coordinates, setCode }) {
  return (
    <>
      <div className="coord-output">
        {coordinates}
      </div>

      <button className="btn-back" onClick={() => setCode(null)}>&laquo; Back</button>
    </>
  )
}

export default App;
